.Btn {
  display: inline-block;
  font-weight: 500;
  color: #666666;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.6125rem 1.875rem;
  font-size: 1rem;
  line-height: 1.4375;
  border-radius: 5px;
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.Link {
  color: #000000;
  text-decoration: none;
  background-color: transparent;
}

.Btn:hover {
  color: #666666;
  text-decoration: none;
}

.Btn.disabled,
.Btn:disabled {
  opacity: 0.65;
  box-shadow: none;
  pointer-events: none;
  cursor: none;
}

.BtnDark {
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
  box-shadow: none;
}

.BtnDark:hover {
  color: #fff;
  background-color: #23272b;
  border-color: #1d2124;
}

.BtnDark.disabled,
.BtnDark:disabled {
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
}
.BtnOutlinePrimary {
  color: #000000;
  border-color: #000000;
}

.BtnOutlinePrimary:hover {
  color: #fff;
  background-color: #000000;
  border-color: #000000;
}

.BtnOutlinePrimary:focus,
.BtnOutlinePrimary.focus {
  box-shadow: 0 0 0 0.2rem rgba(0, 0, 0, 0.5);
}

.BtnOutlinePrimary.disabled,
.BtnOutlinePrimary:disabled {
  color: #000000;
  background-color: transparent;
}

.BtnFullWidth {
  width: 100%;
}
